.header-color {
  background: #1a2a6c;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #fdbb2d, #b21f1f, #1a2a6c);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #fdbb2d, #b21f1f, #1a2a6c); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid {
  background: #1a2a6c;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #fdbb2d, #b21f1f, #1a2a6c);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #fdbb2d, #b21f1f, #1a2a6c); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */  
  text-align: center;
  position: fixed;
  top:  0;
  left: 0;
  width: 100%;
  height: 100%;
}

.avatar-img {
  height: 250px;
  padding-top: 5em;
  padding-bottom: 1em;
  border-radius: 40%;
}

.banner-text {
  background-color: black;
  opacity: .8;
  width: 60%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1 {
  font-size: 46px;
  font-family: 'Concert One', cursive;
  color: white;
  margin: auto;
}

.banner-text hr {
  border-top: 5px dashed white;
  width: 85%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-family: 'Space Mono', monospace;
  font-size: 30px;
  padding: 1em;
  margin: auto;
}

.social-links{
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}

/* Projects Page */

.projects-grid {
  display: flex;
}

/* Contact Page */

.contact-body {
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center; 
  width: 80%;
  height: 550px;
  background: white;
  margin-bottom: 20px;
}

.contact-grid h2 {
  font-family: 'Anton', sans-serif;
}

.contact-grid p {
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
  font-size: 15px;
}

.contact-grid hr {
  border-top: 5px dashed black;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 60px;
  padding-right: 4rem;
}

/* Resume Page */

.resume-right-col {
  background: #27221f;
  color: white;
  padding: 2em;
}

/* GMap page */
.google-map-div li {
  cursor: pointer;
}